import './App.css';
import Footer from './components/Footer';
import Header from './components/Header';
import Home from './components/Home';
import QuemSomos from './components/QuemSomos';
import Transparencia from './components/Transparencia';
import EscolhaContribuicaoLGBTQIAP from './components/EscolhaContribuicaoLGBTQIAP';
import EscolhaContribuicaoCausaAnimal from './components/EscolhaContribuicaoCausaAnimal';
import EscolhaContribuicaoSituacaoDeRua from './components/EscolhaContribuicaoSituacaoDeRua';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
  useLocation
} from "react-router-dom";
import ApoieUmaCausa from './components/HomeComponents/ApoieUmaCausa';
import NossosParceiros from './components/HomeComponents/NossosParceiros';
import ScrollToTop from './components/ScrollToTop';

function App() {
  return (
    <div className="App">
      <Router>
        <ScrollToTop/>
        <Header/>
        <Routes>
         
          {/* Rotas para deploy*/}
          {/* <Route exact path="/GO/" element={<Home/>} />
          <Route path="/GO/quem-somos" element={<QuemSomos/>}/>
          <Route path="/GO/transparencia" element={<Transparencia/>}/>
          <Route path="/GO/contribuicaoLGBT" element={<EscolhaContribuicaoLGBTQIAP/>}/>
          <Route path="/GO/contribuicaoCausaAnimal" element={<EscolhaContribuicaoCausaAnimal/>}/>
          <Route path="/GO/contribuicaoSituacaoDeRua" element={<EscolhaContribuicaoSituacaoDeRua/>}/>
          <Route path="/GO/apoie-uma-causa" element={<ApoieUmaCausa/>}/>
          <Route path="/GO/nossos-parceiros" element={<NossosParceiros/>}/>  */}
          
         

          {/* Rotas para local-host */}
          
          <Route exact path="/" element={<Home/>} />
          <Route path="/quem-somos" element={<QuemSomos/>}/>
          <Route path="/transparencia" element={<Transparencia/>}/>
          <Route path="/contribuicaoLGBT" element={<EscolhaContribuicaoLGBTQIAP/>}/>
          <Route path="/contribuicaoCausaAnimal" element={<EscolhaContribuicaoCausaAnimal/>}/>
          <Route path="/contribuicaoSituacaoDeRua" element={<EscolhaContribuicaoSituacaoDeRua/>}/>
          <Route path="/apoie-uma-causa" element={<ApoieUmaCausa/>}/>
          <Route path="/nossos-parceiros" element={<NossosParceiros/>}/> 
           
        </Routes>
        <Footer/>
      </Router>
    </div>
  );
}

export default App;
